import MessageTop from '../../components/messageTip/index.vue'
export default {
	components: {
		MessageTop
	},
	data() {
		const currentDate = this.getDate({
				format: true
		})
		return {
			date: currentDate,
			title: '',
			content: '',
			isImageShow: false,
			imageList: [],
			swiperCurrent: 0,
			isShowMessage: false,
			isMessage: ''
		}
	},
	methods: {
		bindDateChange: function(e) {
			this.date = e.target.value
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
					year = year - 60;
			} else if (type === 'end') {
					year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		uploadImage () {
			const imglist = this.imageList;
			uni.chooseImage({
			    count: 1,
			    sourceType: ['album'],
			    success: (res) => {
			        uni.getImageInfo({
			            src: res.tempFilePaths[0],
			            success: (imageMessage) => {
										const { path } = imageMessage
										imglist.push(path)
										this.imageList = imglist
										this.isImageShow = true;
			            }
			        });
			    }
			});
		},
		swiperChange(event) {
			const { detail } = event;
			const { current } = detail;
			this.swiperCurrent = current;
		},
		reduceImage () {
			if (this.imageList.length > 1) {
				const newImageList = this.imageList.splice(this.swiperCurrent, 1);
				this.swiperCurrent = this.swiperCurrent === 0 ? 0 : this.swiperCurrent - 1;
			} else {
				this.isImageShow = false;
			}
		},
		getAddress () {
			uni.navigateTo({
				url: '../address/address',
			})
		},
		hideMessage () {
			this.isShowMessage = false
		},
		save () {
			if (!this.title) {
				this.isShowMessage = true;
				this.isMessage = '请输入标题！';
				setTimeout(() => {
					this.isShowMessage = false;
				}, 2000)
			} else if (!this.content) {
				this.isShowMessage = true;
				this.isMessage = '请输入正文！';
			} else {
				const diaryMessage = {
					date: this.date,
					title: this.title,
					content: this.content,
					images: this.imageList,
					id: Math.round(Math.random() * 1000)
				}
				const storageDiaryList = uni.getStorageSync('diary_list');
				if (storageDiaryList) {
					storageDiaryList.push(diaryMessage)
					uni.setStorageSync('diary_list', storageDiaryList);
					uni.setStorage({
					    key: 'diary_list',
					    data: storageDiaryList,
					    success: function () {
								uni.switchTab({
									url: '../list/list'
								})
					    }
					});
				} else {
					const newDiaryList = [];
					newDiaryList.push(diaryMessage)
					uni.setStorageSync('diary_list', newDiaryList);
					uni.setStorage({
					    key: 'diary_list',
					    data: newDiaryList,
					    success: function () {
								uni.switchTab({
									url: '../list/list'
								})
					    }
					});
				}
			}
			
		}
	}
}
